'use client';

import React, { Component, ReactNode } from 'react';
import { AppError, ErrorCategory, ErrorSeverity, ErrorHandler, createContext } from '@/utils/error';

// Í∏ÄÎ°úÎ≤å ÏóêÎü¨ Ïª®ÌÖçÏä§Ìä∏ ÌÉÄÏûÖ (Îü∞ÌÉÄÏûÑÏóêÏÑú ÏÇ¨Ïö©)
interface GlobalErrorHandler {
  addError?: (error: AppError | Error | string, options?: { autoHide?: boolean; duration?: number }) => string;
}

// Í∏ÄÎ°úÎ≤å ÏóêÎü¨ Ìï∏Îì§Îü¨ Ï∞∏Ï°∞ (Context Ïô∏Î∂ÄÏóêÏÑú Ï†ëÍ∑º Í∞ÄÎä•)
let globalErrorHandler: GlobalErrorHandler = {};

// Í∏ÄÎ°úÎ≤å ÏóêÎü¨ Ìï∏Îì§Îü¨ Îì±Î°ù Ìï®Ïàò
export function registerGlobalErrorHandler(handler: GlobalErrorHandler) {
  globalErrorHandler = handler;
}

/**
 * ÏóêÎü¨ Î∞îÏö¥ÎçîÎ¶¨ Props Ïù∏ÌÑ∞ÌéòÏù¥Ïä§
 */
interface ErrorBoundaryProps {
  children: ReactNode;
  fallback?: (error: AppError, retry: () => void) => ReactNode;
  onError?: (error: AppError) => void;
  level?: 'page' | 'section' | 'component';
  identifier?: string;
  enableRetry?: boolean;
  maxRetries?: number;
  retryDelay?: number;
  propagateToGlobal?: boolean; // Í∏ÄÎ°úÎ≤å ÏóêÎü¨ ÏÉÅÌÉúÎ°ú Ï†ÑÌååÌï†ÏßÄ Ïó¨Î∂Ä
}

/**
 * ÏóêÎü¨ Î∞îÏö¥ÎçîÎ¶¨ State Ïù∏ÌÑ∞ÌéòÏù¥Ïä§
 */
interface ErrorBoundaryState {
  hasError: boolean;
  error: AppError | null;
  errorId: string | null;
  retryCount: number;
  isRetrying: boolean;
}

/**
 * Í∏∞Î≥∏ ÏóêÎü¨ Fallback UI Ïª¥Ìè¨ÎÑåÌä∏
 */
function DefaultErrorFallback({ 
  error, 
  retry, 
  level = 'component',
  enableRetry = true,
  retryCount,
  maxRetries,
  isRetrying
}: { 
  error: AppError; 
  retry: () => void; 
  level?: 'page' | 'section' | 'component';
  enableRetry?: boolean;
  retryCount: number;
  maxRetries: number;
  isRetrying: boolean;
}) {
  const canRetry = enableRetry && retryCount < maxRetries;

  const getErrorIcon = () => {
    switch (error.severity) {
      case ErrorSeverity.CRITICAL:
      case ErrorSeverity.HIGH:
        return 'üö®';
      case ErrorSeverity.MEDIUM:
        return '‚ö†Ô∏è';
      case ErrorSeverity.LOW:
        return '‚ÑπÔ∏è';
      default:
        return '‚ùå';
    }
  };

  const getContainerClasses = () => {
    switch (level) {
      case 'page':
        return 'min-h-screen flex items-center justify-center bg-gray-50';
      case 'section':
        return 'min-h-[400px] flex items-center justify-center bg-gray-50 rounded-lg';
      case 'component':
        return 'min-h-[200px] flex items-center justify-center bg-gray-50 rounded border';
      default:
        return 'min-h-[200px] flex items-center justify-center bg-gray-50 rounded';
    }
  };

  const getContentClasses = () => {
    switch (level) {
      case 'page':
        return 'text-center max-w-md';
      case 'section':
        return 'text-center max-w-sm';
      default:
        return 'text-center max-w-xs';
    }
  };

  return (
    <div className={getContainerClasses()}>
      <div className={getContentClasses()}>
        <div className="text-4xl mb-4">{getErrorIcon()}</div>
        <h2 className="text-lg font-semibold text-gray-800 mb-2">
          {level === 'page' ? 'ÌéòÏù¥ÏßÄ Ïò§Î•ò' : 'Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§'}
        </h2>
        <p className="text-sm text-gray-600 mb-4">
          {error.toUserMessage()}
        </p>
        {error.isRetryable && (
          <div className="space-y-2">
            <button
              onClick={retry}
              disabled={isRetrying}
              className="px-4 py-2 bg-blue-600 text-white text-sm rounded-md hover:bg-blue-700 transition-colors focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-offset-2 disabled:opacity-50 disabled:cursor-not-allowed"
            >
              {isRetrying ? 'Ïû¨ÏãúÎèÑ Ï§ë...' : `Îã§Ïãú ÏãúÎèÑ (${retryCount}/${maxRetries})`}
            </button>
          </div>
        )}
        {process.env.NODE_ENV === 'development' && (
          <details className="mt-4 text-left">
            <summary className="text-xs text-gray-500 cursor-pointer">
              Í∞úÎ∞úÏûê Ï†ïÎ≥¥
            </summary>
            <pre className="text-xs text-gray-400 mt-2 overflow-auto">
              {JSON.stringify(error.toLogData(), null, 2)}
            </pre>
          </details>
        )}
      </div>
    </div>
  );
}

/**
 * React Error Boundary Ïª¥Ìè¨ÎÑåÌä∏
 * 
 * Ï§ëÏïôÌôîÎêú ÏóêÎü¨ Ìï∏Îì§ÎßÅ ÏãúÏä§ÌÖúÍ≥º ÌÜµÌï©Îêú ÏóêÎü¨ Î∞îÏö¥ÎçîÎ¶¨ÏûÖÎãàÎã§.
 * Next.js App RouterÏôÄ Ìò∏ÌôòÎêòÎ©∞, Îã§ÏñëÌïú Î†àÎ≤®ÏóêÏÑú ÏÇ¨Ïö©Ìï† Ïàò ÏûàÏäµÎãàÎã§.
 */
export class ErrorBoundary extends Component<ErrorBoundaryProps, ErrorBoundaryState> {
  private retryTimeoutId: NodeJS.Timeout | null = null;

  constructor(props: ErrorBoundaryProps) {
    super(props);
    this.state = {
      hasError: false,
      error: null,
      errorId: null,
      retryCount: 0,
      isRetrying: false,
    };
  }

  static getDerivedStateFromError(error: Error): Partial<ErrorBoundaryState> {
    // ÏóêÎü¨ ID ÏÉùÏÑ± (ÎîîÎ≤ÑÍπÖÏö©)
    const errorId = `error_${Date.now()}_${Math.random().toString(36).substr(2, 9)}`;
    
    return {
      hasError: true,
      errorId,
    };
  }

  async componentDidCatch(error: Error, errorInfo: React.ErrorInfo) {
    try {
      // ÏóêÎü¨ Ïª®ÌÖçÏä§Ìä∏ ÏÉùÏÑ±
      const context = createContext()
        .setUrl(typeof window !== 'undefined' ? window.location.href : '')
        .setUserAgent(typeof window !== 'undefined' ? window.navigator.userAgent : '')
        .setAdditionalData({
          componentStack: errorInfo.componentStack,
          errorBoundary: this.props.identifier || 'unknown',
          level: this.props.level || 'component',
          errorId: this.state.errorId,
          retryCount: this.state.retryCount,
        })
        .build();

      // Ï§ëÏïôÌôîÎêú ÏóêÎü¨ Ìï∏Îì§Îü¨Î°ú Ï≤òÎ¶¨
      const appError = await ErrorHandler.handle(error, context);
      
      this.setState({ error: appError });

      // Í∏ÄÎ°úÎ≤å ÏóêÎü¨ ÏÉÅÌÉúÎ°ú Ï†ÑÌåå (ÏòµÏÖòÏù¥ ÌôúÏÑ±ÌôîÎêú Í≤ΩÏö∞)
      if (this.props.propagateToGlobal !== false && globalErrorHandler.addError) {
        const shouldAutoHide = this.props.level !== 'page'; // ÌéòÏù¥ÏßÄ Î†àÎ≤® ÏóêÎü¨Îäî ÏûêÎèô Ïà®ÍπÄ ÏïàÌï®
        const duration = this.props.level === 'page' ? undefined : 8000; // ÌéòÏù¥ÏßÄ ÏóêÎü¨Îäî ÏàòÎèô Ìï¥Ï†ú
        
        globalErrorHandler.addError(appError, {
          autoHide: shouldAutoHide,
          duration: duration,
        });
      }

      // ÏÇ¨Ïö©Ïûê Ï†ïÏùò ÏóêÎü¨ Ìï∏Îì§Îü¨ Ìò∏Ï∂ú
      this.props.onError?.(appError);

    } catch (handlingError) {
      console.error('ÏóêÎü¨ Î∞îÏö¥ÎçîÎ¶¨ÏóêÏÑú ÏóêÎü¨ Ï≤òÎ¶¨ Ï§ë Ïò§Î•ò:', handlingError);
      
      // Ìè¥Î∞± ÏóêÎü¨ ÏÉùÏÑ±
      const fallbackError = new AppError(
        error.message || 'Ïïå Ïàò ÏóÜÎäî Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.',
        ErrorCategory.UNKNOWN,
        ErrorSeverity.MEDIUM,
        500,
        { originalError: error }
      );
      
      this.setState({ error: fallbackError });

      // Ìè¥Î∞± ÏóêÎü¨ÎèÑ Í∏ÄÎ°úÎ≤åÎ°ú Ï†ÑÌåå
      if (this.props.propagateToGlobal !== false && globalErrorHandler.addError) {
        globalErrorHandler.addError(fallbackError);
      }
    }
  }

  componentWillUnmount() {
    if (this.retryTimeoutId) {
      clearTimeout(this.retryTimeoutId);
    }
  }

  /**
   * ÏóêÎü¨ ÏÉÅÌÉú Ï¥àÍ∏∞Ìôî Î∞è Ïû¨ÏãúÎèÑ
   */
  handleRetry = () => {
    // Îπ†Î•∏ Ïó∞ÏÜç Ïû¨ÏãúÎèÑ Î∞©ÏßÄ
    if (this.retryTimeoutId) {
      return;
    }

    // ÏµúÎåÄ Ïû¨ÏãúÎèÑ ÌöüÏàò ÌôïÏù∏ (3Ìöå)
    if (this.state.retryCount >= 3) {
      console.warn('ÏµúÎåÄ Ïû¨ÏãúÎèÑ ÌöüÏàòÏóê ÎèÑÎã¨ÌñàÏäµÎãàÎã§.');
      return;
    }

    // Ïû¨ÏãúÎèÑ Í∞ÑÍ≤© Í≥ÑÏÇ∞ (ÏßÄÏàò Î∞±Ïò§ÌîÑ)
    const retryDelay = Math.min(1000 * Math.pow(2, this.state.retryCount), 8000);

    this.setState(prevState => ({
      hasError: false,
      error: null,
      errorId: null,
      retryCount: prevState.retryCount + 1,
      isRetrying: true,
    }));

    // ÏßÄÏó∞ ÌõÑÏóê Ïû¨ÏãúÎèÑ Í∞ÄÎä•ÌïòÎèÑÎ°ù ÏÑ§Ï†ï
    this.retryTimeoutId = setTimeout(() => {
      this.retryTimeoutId = null;
    }, retryDelay);
  };

  render() {
    if (this.state.hasError && this.state.error) {
      // ÏÇ¨Ïö©Ïûê Ï†ïÏùò Fallback UIÍ∞Ä ÏûàÏúºÎ©¥ ÏÇ¨Ïö©
      if (this.props.fallback) {
        return this.props.fallback(this.state.error, this.handleRetry);
      }

      // Í∏∞Î≥∏ Fallback UI ÏÇ¨Ïö©
      return (
        <DefaultErrorFallback
          error={this.state.error}
          retry={this.handleRetry}
          level={this.props.level}
          enableRetry={this.props.enableRetry}
          retryCount={this.state.retryCount}
          maxRetries={this.props.maxRetries || 3}
          isRetrying={this.state.isRetrying}
        />
      );
    }

    return this.props.children;
  }
}

/**
 * ÌéòÏù¥ÏßÄ Î†àÎ≤® ÏóêÎü¨ Î∞îÏö¥ÎçîÎ¶¨ Ïª¥Ìè¨ÎÑåÌä∏
 */
export function PageErrorBoundary({ 
  children, 
  fallback,
  onError,
  identifier 
}: Omit<ErrorBoundaryProps, 'level'>) {
  return (
    <ErrorBoundary
      level="page"
      fallback={fallback}
      onError={onError}
      identifier={identifier}
    >
      {children}
    </ErrorBoundary>
  );
}

/**
 * ÏÑπÏÖò Î†àÎ≤® ÏóêÎü¨ Î∞îÏö¥ÎçîÎ¶¨ Ïª¥Ìè¨ÎÑåÌä∏
 */
export function SectionErrorBoundary({ 
  children, 
  fallback,
  onError,
  identifier 
}: Omit<ErrorBoundaryProps, 'level'>) {
  return (
    <ErrorBoundary
      level="section"
      fallback={fallback}
      onError={onError}
      identifier={identifier}
    >
      {children}
    </ErrorBoundary>
  );
}

/**
 * Ïª¥Ìè¨ÎÑåÌä∏ Î†àÎ≤® ÏóêÎü¨ Î∞îÏö¥ÎçîÎ¶¨ Ïª¥Ìè¨ÎÑåÌä∏
 */
export function ComponentErrorBoundary({ 
  children, 
  fallback,
  onError,
  identifier 
}: Omit<ErrorBoundaryProps, 'level'>) {
  return (
    <ErrorBoundary
      level="component"
      fallback={fallback}
      onError={onError}
      identifier={identifier}
    >
      {children}
    </ErrorBoundary>
  );
}

/**
 * ÎπÑÎèôÍ∏∞ Ïª¥Ìè¨ÎÑåÌä∏Ïö© ÏóêÎü¨ Î∞îÏö¥ÎçîÎ¶¨ HOC
 */
export function withErrorBoundary<P extends object>(
  Component: React.ComponentType<P>,
  errorBoundaryProps?: Omit<ErrorBoundaryProps, 'children'>
) {
  const WrappedComponent = (props: P) => (
    <ErrorBoundary {...errorBoundaryProps}>
      <Component {...props} />
    </ErrorBoundary>
  );

  WrappedComponent.displayName = `withErrorBoundary(${Component.displayName || Component.name})`;
  
  return WrappedComponent;
}

export default ErrorBoundary; 